<template>
  <div class="main">
    <header>
    <div class="top">
      <p> 我的天教务系统---管理端</p>
      <el-button @click="goToLoginPage">注销</el-button>
    </div>
    <footer>
      <p>类型：&nbsp;管理员&nbsp;&nbsp;&nbsp;&nbsp;
        姓名&nbsp;:&nbsp;{{admin_name}}&nbsp;&nbsp;&nbsp;&nbsp;
        工号&nbsp;:&nbsp;{{admin_id}}</p>
    </footer>
  </header>
    <div class="kongbai"></div>
    <aside>
      <el-button class="sidebarbox" @click="changeIsShow('addstudent')" style="width:50%;min-width:160px;margin:10px 0;" ><li>添加学生</li></el-button>
      <el-button class="sidebarbox" @click="changeIsShow('addteacher')" style="width:50%;min-width:160px;margin:10px 0;"><li>添加教师</li></el-button>
      <el-button class="sidebarbox" @click="changeIsShow('amentmsg')" style="width:50%;min-width:160px;margin:10px 0;"><li>修改人员信息</li></el-button>
    </aside>
    <div class="mainbox">
      <div v-if="type.length===0" >
          <div class="school_news" >学校新闻</div>
          <div class="person_news" >个人通知</div>
      </div>
      <!--组件-->
      <addStudent v-if="type==='addstudent'"></addStudent>
      <addTeacher v-if="type==='addteacher'"></addTeacher>
      <amentMsg v-if="type==='amentmsg'"></amentMsg>
    </div>
  </div>
</template>

<script>

  import addStudent from './addStudent'
  import addTeacher from './addTeacher'
  import amentMsg from './amentMsg'

    export default {
        name: "adminPage",

      data(){
          return{
            admin_id:"",
            admin_name:"",
            usertype: "",
            type:""
          }
      },
      //生命周期
      mounted() {
          console.log("creatdone");
          this.adminMsg();
          },

      //引入组件
      components:{
        addStudent:addStudent,
        addTeacher:addTeacher,
        amentMsg:amentMsg
      },

      methods: {

          //获取个人信息
          adminMsg(){
            const result = JSON.parse(localStorage.getItem('result'));
            console.log(result);
            this.admin_id = result.data[0].admin_id;
            this.admin_name = result.data[0].admin_name;
          },

        //组件切换
        changeIsShow(compoment_name){
          this.type = compoment_name
        },

        //登出
        goToLoginPage() {
          if (confirm("是否登出")) {
            this.$router.push({path: '/'})
          }
          else {
            return
          }
        },

      }
    }
</script>

<style scoped>
  @import "../main_style.css";
</style>



