//提交前判断
export function testBeforeSubmit(that){

    if( that.form1.student_name===""){
      alert("请输入姓名");return
    }if(that.form1.student_class===""){
      alert("请输入班级");return
    }if(that.form1.student_department===""){
      alert("请输入学院") ;return
    }if(that.form1.student_id===""){
      alert("请输入学号");return
    }if(that.form1.student_birthplace===""){
      alert("请输入出生地点");return
    }if(that.form1.student_address===""){
      alert("请输入家庭住址");return
    }if(that.form1.student_join_time===""){
      alert("请输入入学时间");return
    }if(that.form1. student_sex===""){
      alert("请输入性别");return
    }if(that.form1.student_political_status===""){
      alert("请输入政治面貌");return
    }if(that.form1.student_idcard===""){
      alert("请输入身份号");return
    }if(that.form1.student_major===""){
      alert("请输入专业");return
    }if(that.form1.student_from===""){
      alert("请输入生源类型");return
    }
  }


export function testBeforeSubmit2(that) {

  if (that.form2.teacher_name === "") {
    alert("请输入姓名");return
  }if (that.form2.teacher_id === "") {
    alert("请输入账号");return
  }if (that.form2.teacher_department === "")
  {alert("请输入学院");return
  }if (that.form2.teacher_major === "") {
    alert("请输入专业");return
  }if (that.form2.teacher_join_time === "") {
    alert("请输入入职时间");return}
}



