<template>
      <div>
        <div v-if="inputbox">
          <div>
            <el-form ref="form" label-width="80px">
              <el-form-item label="学号/工号">
                <el-input v-model="number"></el-input>
              </el-form-item>
            </el-form>
          </div>
            <el-button @click=" testBeforeAmnmt">确定</el-button>
         </div>
        <div v-if="student">
          <el-form ref="form1" :model="form1" label-width="80px" >
            <el-form-item label="学生姓名">
              <el-input v-model="form1.student_name"></el-input>
            </el-form-item>

            <el-form-item label="学生班级">
              <el-input v-model="form1.student_class" ></el-input>
            </el-form-item>

            <el-form-item label="学院">
              <el-select v-model="form1.student_department" placeholder="请选择学院" style="width:326px;">
                <el-option label="理学院" value="理学院"></el-option>
                <el-option label="光电工程学院" value="光电工程学院"></el-option>
                <el-option label="机电工程学院" value="机电工程学院"></el-option>
                <el-option label="电子信息工程学院" value="电子信息工程学院"></el-option>
                <el-option label="计算机科学技术学院" value="计算机科学技术学院"></el-option>
                <el-option label="人工智能学院" value="人工智能学院"></el-option>
                <el-option label="材料科学与工程学院" value="材料科学与工程学院"></el-option>
                <el-option label="化学与环境工程学院" value="化学与环境工程学院"></el-option>
                <el-option label="生命科学技术学院" value="生命科学技术学院"></el-option>
                <el-option label="经济管理学院" value="经济管理学院"></el-option>
                <el-option label="外国语学院" value="外国语学院"></el-option>
                <el-option label="法学院" value="法学院"></el-option>
                <el-option label="马克思主义学院" value="马克思主义学院"></el-option>
              </el-select>
            </el-form-item>

            <el-form-item label="学生专业">
              <el-input v-model="form1.student_major"></el-input>
            </el-form-item>

            <el-form-item label="生源类型">
              <el-select v-model="form1.student_from" placeholder="请选择类型" style="width:326px;">
                <el-option label="城镇应届" value="城镇应届"></el-option>
                <el-option label="农村应届" value="农村应届"></el-option>
                <el-option label="城镇往届" value="城镇往届"></el-option>
                <el-option label="农村往届" value="农村往届"></el-option>
              </el-select>
            </el-form-item>

            <el-form-item label="学号">
              <el-input v-model="form1.student_id"></el-input>
            </el-form-item>

            <el-form-item label="家庭住址">
              <el-input v-model="form1.student_address"></el-input>
            </el-form-item>

            <el-form-item label="出生地点">
              <el-input v-model="form1.student_birthplace"></el-input>

            </el-form-item>

            <el-form-item label="入学时间">
              <el-col :span="11">
                <el-date-picker type="date" placeholder="选择日期" v-model="form1.student_join_time" style="width:326px;"></el-date-picker>
              </el-col>
            </el-form-item>

            <el-form-item label="学生性别">
              <el-radio-group v-model="form1.student_sex">
                <el-radio label="男"></el-radio>
                <el-radio label="女"></el-radio>
              </el-radio-group>
            </el-form-item>

            <el-form-item label="政治面貌">
              <el-radio-group v-model="form1.student_political_status">
                <el-radio label="群众"></el-radio>
                <el-radio label="团员"></el-radio>
                <el-radio label="预备党员"></el-radio>
                <el-radio label="党员"></el-radio>
              </el-radio-group>
            </el-form-item>

            <el-form-item label="身份证号">
              <el-input v-model="form1.student_idcard"></el-input>
            </el-form-item>
          </el-form >
          <div>
            <el-button type="primary" @click="testBeforeSubmitTo()">提交</el-button>
          </div>
        </div>
        <div v-if="teacher">
          <el-form ref="form1" :model="form2" label-width="80px">
            <el-form-item label="教师姓名">
              <el-input v-model="form2.teacher_name"></el-input>
            </el-form-item>

            <el-form-item label="教师账号">
              <el-input v-model="form2.teacher_id" ></el-input>
            </el-form-item>

            <el-form-item label="学院">
              <el-select v-model="form2.teacher_department" placeholder="请选择学院" style="width:326px;">
                <el-option label="理学院" value="理学院"></el-option>
                <el-option label="光电工程学院" value="光电工程学院"></el-option>
                <el-option label="机电工程学院" value="机电工程学院"></el-option>
                <el-option label="电子信息工程学院" value="电子信息工程学院"></el-option>
                <el-option label="计算机科学技术学院" value="计算机科学技术学院"></el-option>
                <el-option label="人工智能学院" value="人工智能学院"></el-option>
                <el-option label="材料科学与工程学院" value="材料科学与工程学院"></el-option>
                <el-option label="化学与环境工程学院" value="化学与环境工程学院"></el-option>
                <el-option label="生命科学技术学院" value="生命科学技术学院"></el-option>
                <el-option label="经济管理学院" value="经济管理学院"></el-option>
                <el-option label="外国语学院" value="外国语学院"></el-option>
                <el-option label="法学院" value="法学院"></el-option>
                <el-option label="马克思主义学院" value="马克思主义学院"></el-option>
              </el-select>
            </el-form-item>

            <el-form-item label="教师专业">
              <el-input v-model="form2.teacher_major"></el-input>
            </el-form-item>



            <el-form-item label="入职时间">
              <el-col :span="11">
                <el-date-picker type="date" placeholder="选择日期" v-model="form2.teacher_join_time" style="width:326px;"></el-date-picker>
              </el-col>
            </el-form-item>

          </el-form>
          <div>
            <el-button type="primary" @click="testBeforeSubmitTo()">提交</el-button>
          </div>
        </div>


    </div>
</template>

<script>
  import {amemtMsg,sentMsg} from "../../axios/api";
  import {testBeforeSubmit,testBeforeSubmit2} from "../../js/linkStudentMsg"

    export default {
        name: "amentMsg",
      data(){
          return{
            number:'',
            student:false,
            teacher:false,
            inputbox:true,
            formed:{},
            form1:{
              student_name: "",
              student_class: "",
              student_department: "",
              student_id: "",
              student_major: "",
              student_address:"",
              student_birthplace:"",
              student_join_time:"",
              student_sex:"",
              student_from:"",
              student_political_status:"",
              student_idcard:"",
            },
            form2:{
              teacher_name:'',
              teacher_id:'',
              teacher_department:'',
              teacher_major:'',
              teacher_join_time:''
            },
        }
      },

      methods:{

        //  提交前判断
        testBeforeAmnmt(){
          if(this.number===''){
            alert("输入框禁止为空")
            return
          }if(this.number.length==9){
            this.student=true;
            this.inputbox=false;
            this.amemtMsgTo(this.number)
            return
          }if(this.number.length==10){
            this.teacher=true;
            this.inputbox=false;
            this.amemtMsgTo(number)
            return
          }
          alert("请输入格式正确的id")
          return
        },

        //接口
        amemtMsgTo(number) {
          amemtMsg(number).then(amemtresult => {
            console.log(amemtresult)
            if(amemtresult.data.usertype==2){
              this.teacherMsg(amemtresult)
            }else{
              this.studentMsg(amemtresult)
            }
          }).catch(error => {
            console.log(error.response)
          })
        },

        //获取学生信息
        studentMsg(amemtresult) {
          console.log(amemtresult);
          this.form = amemtresult.data[0]
          // this.student_name = amemtresult.data[0].student_name;
          // this.student_class = amemtresult.data[0].student_class;
          // this.student_department = amemtresult.data[0].student_department;
          // this.student_major = amemtresult.data[0].student_major;
          // this.student_id = amemtresult.data[0].student_id;
          // this.student_address= amemtresult.data[0].student_address,
          // this.student_birthplace = amemtresult.data[0].student_birthplace,
          // this.student_join_time = amemtresult.data[0].student_join_time,
          // this.student_sex = amemtresult.data[0].student_sex,
          // this.student_from = amemtresult.data[0].student_from,
          // this.student_political_status = amemtresult.data[0].student_political_status,
          // this.student_idcard = amemtresult.data[0].student_idcard
        },

        //  获取教师信息
        teacherMsg(amemtresult){
          console.log(amemtresult);
          this.form2 = amemtresult.data[0]
        },

        //返回前判断
        testBeforeSubmitTo(){
          if(this.number.length==9){
            let that= this;
            testBeforeSubmit(that);
             this.formed=this.form1;
            this.sentMsgTo(this.formed)
          }else{
            let that= this;
            testBeforeSubmit2(that)
            this.formed=this.form2;
            this.sentMsgTo(this.formed)
          }
        },

      //  返回
        sentMsgTo(){
          sentMsg(this.formed).then(sentresult => {
            console.log(sentresult);
            if(sentresult.length>0){
              alert("修改成功")
            }
          }).catch(error => {
            console.log(error.response)
            alert("请求失败")
          })
        }
        
      }
    }





</script>

<style scoped>

</style>



