<template>
  <div class="bigbox">
    <div  class="header">请在下列输入框中准确输入单个学生信息</div>
    <el-form ref="form" :model="form" label-width="80px">
      <el-form-item label="学生姓名">
        <el-input v-model="form.student_name"></el-input>
      </el-form-item>

      <el-form-item label="学生班级">
        <el-input v-model="form.student_class" ></el-input>
      </el-form-item>

      <el-form-item label="学院">
        <el-select v-model="form.student_department" placeholder="请选择学院" style="width:326px;">
          <el-option label="理学院" value="理学院"></el-option>
          <el-option label="光电工程学院" value="光电工程学院"></el-option>
          <el-option label="机电工程学院" value="机电工程学院"></el-option>
          <el-option label="电子信息工程学院" value="电子信息工程学院"></el-option>
          <el-option label="计算机科学技术学院" value="计算机科学技术学院"></el-option>
          <el-option label="人工智能学院" value="人工智能学院"></el-option>
          <el-option label="材料科学与工程学院" value="材料科学与工程学院"></el-option>
          <el-option label="化学与环境工程学院" value="化学与环境工程学院"></el-option>
          <el-option label="生命科学技术学院" value="生命科学技术学院"></el-option>
          <el-option label="经济管理学院" value="经济管理学院"></el-option>
          <el-option label="外国语学院" value="外国语学院"></el-option>
          <el-option label="法学院" value="法学院"></el-option>
          <el-option label="马克思主义学院" value="马克思主义学院"></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="学生专业">
        <el-input v-model="form.student_major"></el-input>
      </el-form-item>

      <el-form-item label="生源类型">
        <el-select v-model="form.student_from" placeholder="请选择类型" style="width:326px;">
          <el-option label="城镇应届" value="城镇应届"></el-option>
          <el-option label="农村应届" value="农村应届"></el-option>
          <el-option label="城镇往届" value="城镇往届"></el-option>
          <el-option label="农村往届" value="农村往届"></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="学号">
        <el-input v-model="form.student_id"></el-input>
      </el-form-item>

      <el-form-item label="家庭住址">
        <el-input v-model="form.student_address"></el-input>
      </el-form-item>

      <el-form-item label="出生地点">
        <el-input v-model="form.student_birthplace"></el-input>

      </el-form-item>

      <el-form-item label="入学时间">
        <el-col :span="11">
          <el-date-picker type="date" placeholder="选择日期" v-model="form.student_join_time" style="width:326px;"></el-date-picker>
        </el-col>
      </el-form-item>

      <el-form-item label="学生性别">
        <el-radio-group v-model="form.student_sex">
          <el-radio label="男"></el-radio>
          <el-radio label="女"></el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item label="政治面貌">
        <el-radio-group v-model="form.student_political_status">
          <el-radio label="群众"></el-radio>
          <el-radio label="团员"></el-radio>
          <el-radio label="预备党员"></el-radio>
          <el-radio label="党员"></el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item label="身份证号">
        <el-input v-model="form.student_idcard"></el-input>
      </el-form-item>
    </el-form>
    <div>
      <el-button type="primary" @click="testBeforeSubmit">提交</el-button>
    </div>

    <!--<div class="nextHeader">多数人员添加</div>-->
    <!--<upload></upload>-->


  </div>
</template>

<!--submit-->
<script>

import {submitStudent} from "../../axios/api";
import upload from "./upload"

export default {
        name: "addStudent",
      data(){
          return{
            form: {
              student_name: '',
              student_class:'',
              student_department: '',
              student_id:'',
              student_birthplace:'',
              student_address:'',
              student_join_time: '',
              student_sex: '',
              student_political_status:'',
              student_idcard:'',
              student_major:'',
              student_from:'',
            }
        }
      },
  components:{
    upload:upload
  },
      //生命周期
      mounted(){
          console.log("mountedDone")
              console.log(this.form)
      },

      methods:{

      //    提交前验证
        testBeforeSubmit(){
          // const m = {
          //   name: "姓名",
          //   class: "班级",
          // }
          // Object.keys(m).forEach(key => {
          //   if (this.form[`student_${key}`] === '') {
          //     alert(`!!!请输入${m[key]}`);
          //   }
          // });
          if( this.form.student_name===""){
            alert("请输入姓名");return
          }if(this.form.student_class===""){
            alert("请输入班级");return
          }if(this.form.student_department===""){
            alert("请输入学院") ;return
          }if(this.form.student_id===""){
            alert("请输入学号");return
          }if(this.form.student_birthplace===""){
            alert("请输入出生地点");return
          }if(this.form.student_address===""){
            alert("请输入家庭住址");return
          }if(this.form.student_join_time===""){
            alert("请输入入学时间");return
          }if(this.form. student_sex===""){
            alert("请输入性别");return
          }if(this.form.student_political_status===""){
            alert("请输入政治面貌");return
          }if(this.form.student_idcard===""){
            alert("请输入身份号");return
          }if(this.form.student_major===""){
            alert("请输入专业");return
          }if(this.form.student_from===""){
            alert("请输入生源类型");return
          }else{
            this.submitStudentTo(form)
          }

        },

      //接口
        submitStudentTo(form) {
          submitStudent(form).then(submitresult => {
            console.log(submitresult)
            if(submitresult.length>0){
              alert("提交成功")
            }
          }).catch(error => {
            console.log(error.response)
          })
        },

      }



    }
</script>

<style scoped>

  .bigbox {
    max-width: 1000px;

  }
  .header{
    height: 25px;
    margin: 10px;
    font-size: larger;
  }
  .nextHeader{
    height: 25px;
    margin-top: 50px;
    font-size: larger;
  }
</style>



