<template>

  <div class="main">

    <header>
      <div class="top">
        <p>  我的天教务管理系统</p>
        <el-button @click="goToLoginPage">注销</el-button>
      </div>
      <footer>
        <p>学生姓名&nbsp;:&nbsp;{{student_name}}&nbsp;&nbsp;&nbsp;
          学生班级&nbsp;:&nbsp;{{student_class}}&nbsp;&nbsp;&nbsp;
          学生部门&nbsp;:&nbsp;{{student_department}}&nbsp;&nbsp;&nbsp;
          学生专业&nbsp;:&nbsp;{{student_major}}&nbsp;&nbsp;&nbsp;
          学号&nbsp;:&nbsp;{{student_id}}</p>
      </footer>
    </header>
    <div class="kongbai"></div>
    <aside>
      <el-button class="sidebarbox" @click="changeIsShow('info')" style="width:50%;min-width:160px;margin:10px 0;"><li>学籍管理</li></el-button>
      <el-button class="sidebarbox" @click="changeIsShow('manage')" style="width:50%;min-width:160px;margin:10px 0;"><li>选课管理</li></el-button>
      <el-button class="sidebarbox" @click="changeIsShow('test')" style="width:50%;min-width:160px;margin:10px 0;"><li>成绩查询</li></el-button>
      <el-button class="sidebarbox" @click="changeIsShow('schedule_query')" style="width:50%;min-width:160px;margin:10px 0;"><li>课表查询</li></el-button>
    </aside>
    <div class="mainbox">
      <div class="school_news">学校新闻</div>
      <div class="person_news">个人通知</div>
    </div>
    <div class="mainbox">
      <studentInfo  v-if="type==='info'"></studentInfo>
      <testQuery  v-if="type==='test'"></testQuery>
      <!--<query v-bind:class={mainbox:show==='query'}></query>-->
      <!--<query2 v-bind:class={mainbox:show==='query2'}></query2>-->
    </div>

    <div class="back">
      <el-button @click="goToLoginPage">注销</el-button>
    </div>
  </div>

</template>


<script>

  import studentInfo from './studentInfo'
  import testQuery from './testQuery'

  export default {
    name: "mainPage",

    data() {
      return {
        student_name: "",
        student_class: "",
        student_department: "",
        student_id: "",
        student_major: "",
        type:"",
      }
    },

    mounted(){
      this.studentMsg();
      console.log("createdDone");
    },
    components:{
      'testQuery':testQuery,
      'studentInfo':studentInfo,
    },
    methods: {

      //获取个人信息
      studentMsg() {
        const result = JSON.parse(localStorage.getItem('result'));
        // console.log(result);
        this.student_name = result.data[0].student_name;
        this.student_class = result.data[0].student_class;
        this.student_department = result.data[0].student_department;
        this.student_major = result.data[0].student_major;
        this.student_id = result.data[0].student_id;
      },

      //组件切换
      changeIsShow(compoment_name){
        this.type = compoment_name

      },

      //登出
      goToLoginPage() {
        if (confirm("是否登出")) {
          this.$router.push({path: '/'})
        }
        else {
          return
        }
      },
    },

  }
</script>

<style scoped>
  @import "../main_style.css";


</style>



