<template>
    <div>
      <div class="nonebox"></div>
      <div v-if="show">
        <div  v-for="item in items">
          <div class="sidebar">
            <el-button class="sidebarbox" >
            <div class="smallbox">科目:{{item.course_name}}</div>
            <div class="smallbox">分数:{{item.course_score}}&nbsp;&nbsp;&nbsp;类别:{{item.type}}&nbsp;&nbsp;&nbsp;学分:{{item.credit}}</div>
            <div class="smallbox">学时:{{item.class_hour}}&nbsp;&nbsp;&nbsp;状态:{{item.status}}</div>
            </el-button>
         </div>
       </div>
     </div>

    </div>
</template>

<script>

  import {chickTestQuery} from "../../axios/api"

  export default {
        name: "testQuery",
      data(){
          return{
            url:'http://144.202.108.147/',
            key:"",
            items:"test_result.data",
            show:0

          }
        },

      //   生命周期
      mounted(){
          this.getTheKey();
          this.theChickTestQuery(this.key);
          console.log("mounted done")
      },
        methods:{

            //    获取key
            getTheKey(){
              const result = JSON.parse(localStorage.getItem('result'));
              console.log(result);
              this.key = result.data[0].student_key;
              console.log(this.key)
            },

            //    获取成绩
            theChickTestQuery(key) {
              chickTestQuery(key).then(test_result => {
                console.log(test_result.data)
                this.items = test_result.data
                if(test_result.data){
                  this.show=1
                }
                else{
                  alert("成绩未上传")
                }
              }).catch(error => {
                console.log(error.response)
              })
             },


        }


    }
</script>

<style scoped>

  .nonebox{
    height: 15px;
  }
  .sidebarbox{
    width: 350px;
    margin: 5px;
  }

  .smallbox{
    margin: 7px;
  }

</style>



