export function setDPR () {
  var viewport = document.querySelector('meta[name=viewport]'), zoom = 1 / (window.devicePixelRatio || 1);
  viewport.setAttribute('content', 'width=device-width,initial-scale=' + zoom + ', maximum-scale=' + zoom + ', minimum-scale=' + zoom + ', user-scalable=no');
}
export function remChange () {
  var resizeEvt = 'orientationchange' in window ? 'orientationchange' : 'resize',
    changePage, widthProportion = function () {
      var doc = document.body || document.documentElement, p = doc.clientWidth;
      if (window.screen.width <= 500) {
        return p / 10;
      } else if (doc.clientWidth > 500) {
        return 500 / 10;
      } else {
        return p / 10;
      }
    };
  (changePage = function () {
    document.getElementsByTagName('html')[0].setAttribute('style', 'font-size:' + widthProportion() + 'px !important');
  })();
  window.addEventListener(resizeEvt, changePage, false);
}



