<template>
  <div class="upload-panel">
    <div class="panel-heading">学生导入</div>
    <div class="panel-body">
      <p>
        <strong>注意事项：</strong>
        <a>
        <br>1、导入文件格式：.xls，.xlsx
        <br>2、文件命名规则“年月名”，如：“201705新生”
        </a>
      </p>
      <p style="margin-top:10px;"><strong>导入：</strong>
        <el-button>
        <a class="btn btn-primary btn-xs " @click="chooseFile">选择文件</a>
        </el-button>
      </p>
      <p>已选择文件：<em style="color:red; font-style:normal;">{{attence}}</em></p>
      <input type="file" style="display:none" name="attence"  @change="changeFile($event)" ref="attenceInput" />
    </div>
    <div class="panel-footer">
      <el-button>
      <a class="btn btn-primary btn-md" @click="upFile">确认导入</a>
      </el-button>
    </div>
    <div class="kongbai"></div>
  </div>
</template>



<script>
  export default {
    name: 'Upload',
    data () {
      return {
        attence: '',
        attenceFile: {

        }
      }
    },
    methods: {
      chooseFile () {
        this.$refs.attenceInput.click();
        let temp = document.getElementsByName('attence')
        console.log(temp)
      },
      changeFile (e) {
        this.attence = e.target.files[0].name;
        this.attenceFile = e.target.files[0];
      },
      upFile () {
        let filename = this.attenceFile.name;
        let arr = filename.split('.');
        if (arr[1] !== 'xls' && arr[1] !== 'xlsx') {
          this.$toast.center('文件格式错误！');
          return;
        }
        let fileData = new window.FormData();
        fileData.append('file', file)
        let xhr = new window.XMLHttpRequest();
        xhr.open('POST', 'http://localhost:999/base/upload', true);
        xhr.send(fileData);
        xhr.onreadystatechange = () => {
          if (xhr.readyState === 4) {
            if (xhr.status === 200) {
              let response = JSON.parse(xhr.response)
              this.$toast.center(response.message)        }
            else {
              let error = this.$emit('upload-error', xhr)
              if (error !== false) {
                this.$toast.center(xhr.statusText)
              }
            }
          }
        }
      }
    }
  }</script>


<style scoped>

</style>


<style scoped>
  .upload-panel{
    margin: 25px;
  }

  .kongbai {
    height:200px;
  }

</style>



