<template>
    <div class="main">
      <header>
        <div class="top">
          <p> 我的天教务管理系统---教师端</p>
          <el-button @click="goToLoginPage">注销</el-button>
        </div>
        <footer>
          <p>教师姓名&nbsp;&nbsp;{{teacher_name}}&nbsp;&nbsp;&nbsp;&nbsp;
            教学专业&nbsp;&nbsp;{{teacher_major}}&nbsp;&nbsp;&nbsp;&nbsp;
            教师工号&nbsp;&nbsp;{{teacher_id}}&nbsp;&nbsp;</p>
        </footer>
      </header>
      <div class="kongbai"></div>
      <aside>
        <el-button class="sidebarbox" @click="changeIsShow('info')" style="width:50%;min-width:160px;margin:10px 0;"><li>发布成绩</li></el-button>
        <el-button class="sidebarbox" @click="changeIsShow('manage')" style="width:50%;min-width:160px;margin:10px 0;"><li>选课管理</li></el-button>
      </aside>

      <div class="mainbox">
        <div class="school_news">学校新闻</div>
        <div class="person_news">个人通知</div>
      </div>
      <div class="mainbox">
        <!--组件-->
      </div>
      <div class="back">
        <button @click="goToLoginPage">注销</button>
      </div>
    </div>

</template>

<script>
    export default {
        name: "teacherPage",
      data(){
          return{
            teacher_department: "",
            teacher_id: "",
            teacher_join_time: "",
            teacher_major: "",
            teacher_name: "",
            type:""

          }
      },
      mounted(){
        this.teacherMsg();
        console.log("createdDone");
      },
      methods:{

      //    获取教师信息
        teacherMsg(){
          const result = JSON.parse(localStorage.getItem('result'));
          console.log(result)
            this.teacher_department=result.data[0].teacher_department;
            this.teacher_id=result.data[0].teacher_id;
            this.teacher_join_time=result.data[0].teacher_join_time;
            this.teacher_major=result.data[0].teacher_major;
            this.teacher_name=result.data[0].teacher_name
        },

        //组件切换
        changeIsShow(compoment_name){
          this.type = compoment_name

        },

        //登出
        goToLoginPage() {
          if (confirm("是否登出")) {
            this.$router.push({path: '/'})
          } else {
            return
          }
        }


      }
    }
</script>

<style scoped>
  @import "../main_style.css";


</style>



