<template>



  <div class="hello">

    <div class="maincharacters">
        <div class="characters">我的天教务系统</div>
        <div class="characters2">Teaching Manager Information System of My God</div>
    </div>
          <!--账号-->
    <div class="mainbox">
      <div class="backgroundbox">
        <div class="idbox">
            <li class="el-icon-service"></li>
            <input name="id" id="user-id" v-model="userId"  type="text" placeholder="请输入用户名" auto-complete="on">
        </div>
          <!--密码-->
        <div class="passbox">
            <li class="el-icon-goods"></li>
            <input name="pass" id="user-pass" v-model="userPass" type="password" placeholder="请输入密码" auto-complete="on"
                   @keyup.enter="judgeData(userId, userPass)"
            >
        </div>
        <div  class="wrapper">
            <el-button type="success" @click="judgeData(userId, userPass)">登陆</el-button>
        </div>

      </div>
    </div>
    <div>
      <footer>Design By Detachment of Teaching Manager Information System </footer>
      <footer>2018-2018</footer>
    </div>
  </div>


</template>

<script>

  import {logIn} from "../axios/api"
  import {mapState, mapActions} from 'vuex'

export default {
  name: 'HelloWorld',
  data () {
    return {
      userId:'admin_A',
      userPass:'ewq',
      msg:"",
      url:'http://144.202.108.147/'
    }
  },
  computed: {
    ...mapState([

    ])},
  methods:{

    //登陆前验证
    judgeData(userId,userPass){
      if(userId==""||userPass==""){
        alert("请输入账号或密码")
        return
      } if( /[a-z0-9\u4E00-\u9FFF]{4,20} /.test(userId))
        alert("格式有误");
      else{
       this.getLogIn(userId,userPass)
      }
    },

    //登陆
    getLogIn(userId,userPass){
      logIn(userId,userPass).then(result=>{
        if(result.data.length > 0){
          console.log(result)
          console.log("done")
          //usertype返回 string加引号//  判断用户类型
          switch (result.data[0].usertype) {
            case "0":console.log("admin"+"done");
              this.adminSkip(result)
                break;
            case "1":console.log("student"+"done");
              this.studentSkip(result)
                break;
            case "2":console.log("teacher"+"done");
              this.teacherSkip(result)
                break;
            default: console.log("boom");
          }
          // document.ElementsByName('userId')[0].value ='';
          // document.ElementsByName('userPass')[0].value ='';
        }else{
          console.log(result)
          console.log("error")
          this.userPass=""
          alert("密码错误，请重新输入")
        }
      })
        .catch(error => {
          console.log(error.response)
        })
    },

    //跳转路由
    adminSkip(result){
        console.log(result)
        localStorage.setItem('result', JSON.stringify(result))
        this.$router.push({name:"adminPage"})
    },

  studentSkip(result){
        console.log(result)
        localStorage.setItem('result', JSON.stringify(result))
        this.$router.push({name:"mainPage"})
    },
    teacherSkip(result){
        console.log(result)
        localStorage.setItem('result', JSON.stringify(result))
        this.$router.push({name:"teacherPage"})
    },



  },
}
</script >

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .hello{
    height:100%;
    width:100%;
    /*width: 100%;*/
    border: 1px solid green;
    position:fixed;
    background-image:url("../assets/background.jpg");
    background-size:cover;
  }
  .maincharacters{
    height:120px;
  }
  .characters{
    margin-top:50px;
    font-family: "Adobe 黑体 Std R";
    font-size: 25px;
    color: white;
  }
  .characters2{
    font-family: "Adobe 黑体 Std R";
    margin-top:20px;
    font-size: 20px;
    color: white;
  }
 .mainbox{
   display: -webkit-flex;
   display:         flex;
   -webkit-align-items: center;
   align-items: center;
   -webkit-justify-content: center;
   justify-content: center;
   flex-direction: column;
   min-width: 200px;
   min-height: 70px;
 }
 .backgroundbox{
   width: 400px;
   height: 300px;
   background: rgba(256,256,256,0.5);
   -webkit-border-radius: 20px;
   margin: 50px;

 }
  .idbox,.passbox{
    width: 300px;
    height: 50px;
    margin: 42px;
    text-align: center;
    background: aliceblue;
    border: 2px solid #326795;
    -webkit-border-radius: 10px;

  }
  input{
    width: 250px;
    height: 50px;
    background: rgba(255,255,255,0);
    border:none;
  }
  footer{
    position: relative;
    color: white;
    bottom:10px
  }


</style>



