<template>
  <div class="bigbox">
    <div  class="header">请在下列输入框中准确输入教师信息</div>
    <el-form ref="form" :model="form" label-width="80px">
      <el-form-item label="教师姓名">
        <el-input v-model="form.teacher_name"></el-input>
      </el-form-item>

      <el-form-item label="教师账号">
        <el-input v-model="form.teacher_id" ></el-input>
      </el-form-item>

      <el-form-item label="学院">
        <el-select v-model="form.teacher_department" placeholder="请选择学院" style="width:326px;">
          <el-option label="理学院" value="理学院"></el-option>
          <el-option label="光电工程学院" value="光电工程学院"></el-option>
          <el-option label="机电工程学院" value="机电工程学院"></el-option>
          <el-option label="电子信息工程学院" value="电子信息工程学院"></el-option>
          <el-option label="计算机科学技术学院" value="计算机科学技术学院"></el-option>
          <el-option label="人工智能学院" value="人工智能学院"></el-option>
          <el-option label="材料科学与工程学院" value="材料科学与工程学院"></el-option>
          <el-option label="化学与环境工程学院" value="化学与环境工程学院"></el-option>
          <el-option label="生命科学技术学院" value="生命科学技术学院"></el-option>
          <el-option label="经济管理学院" value="经济管理学院"></el-option>
          <el-option label="外国语学院" value="外国语学院"></el-option>
          <el-option label="法学院" value="法学院"></el-option>
          <el-option label="马克思主义学院" value="马克思主义学院"></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="教师专业">
        <el-input v-model="form.teacher_major"></el-input>
      </el-form-item>



      <el-form-item label="入职时间">
        <el-col :span="11">
          <el-date-picker type="date" placeholder="选择日期" v-model="form.teacher_join_time" style="width:326px;"></el-date-picker>
        </el-col>
      </el-form-item>

    </el-form>
    <div>
      <el-button type="primary" @click="testBeforeSubmit">提交</el-button>
    </div>


  </div>
</template>

<script>
  import {submitTeacher} from "../../axios/api"

    export default {
        name: "addTeacher",
      data()
      {
        return{
          form:{
            teacher_name:'',
            teacher_id:'',
            teacher_department:'',
            teacher_major:'',
            teacher_join_time:''

          }
        }
      },

      //生命周期
      mounted(){
        console.log("mountedDone")
        console.log(this.form)
      },
      methods:{

        //提交前验证
        testBeforeSubmit() {

          if (this.form.teacher_name === "") {
            alert("请输入姓名");return
          }if (this.form.teacher_id === "") {
            alert("请输入账号");return
          }if (this.form.teacher_department === "")
          {alert("请输入学院");return
          }if (this.form.teacher_major === "") {
            alert("请输入专业");return
          }if (this.form.teacher_join_time === "") {
            alert("请输入入职时间");return
          }else {
            this.submitTeachertTo()
          }
        },

        //接口
        submitTeachertTo(form) {
          submitTeacher(form).then(submitresult => {
            console.log(submitresult)
            if(submitresult.length>0){
              alert("提交成功")
            }
          }).catch(error => {
            console.log(error.response)
          })
        },

      }

    }
</script>

<style scoped>

  .bigbox {
    max-width: 1000px;

  }
  .header{
    height: 25px;
    margin: 10px;
    font-size: larger;
  }
  .nextHeader{
    height: 25px;
    margin-top: 50px;
    font-size: larger;
  }
</style>



