<template>

  <div>
    <div class="nonebox"></div>
    <div class="infobox">
          <tr><td>类别</td><td>信息</td></tr>
          <tr><td>姓名</td><td>{{ student_name}}</td></tr>
          <tr><td>班级</td><td>{{student_class}}</td></tr>
          <tr><td>学院</td><td>{{student_department}}</td></tr>
          <tr><td>学号</td><td>{{student_id}}</td></tr>
          <tr><td>专业</td><td>{{student_major}}</td></tr>
          <tr><td>家庭住址</td><td>{{student_address}}</td></tr>
          <tr><td>出生日期</td><td>{{student_birthplace}}</td></tr>
          <tr><td>入学时间</td><td>{{student_join_time}}</td></tr>
          <tr><td>学生性别</td><td>{{student_sex}}</td></tr>
          <tr><td>生源类型</td><td>{{student_from}}</td></tr>
          <tr><td>政治面貌</td><td>{{student_political_status}}</td></tr>
          <tr><td>身份证号</td><td>{{student_idcard}}</td></tr>
    </div>
  </div>


</template>

<script>
    export default {
        name: "studentInfo",
      data() {
        return {
          student_name: "",
          student_class: "",
          student_department: "",
          student_id: "",
          student_major: "",
          student_address:"",
          student_birthplace:"",
          student_join_time:"",
          student_sex:"",
          student_from:"",
          student_political_status:"",
          student_idcard:"",

        }
      },
      mounted(){
        this.studentMsg();
        console.log("createdDone");
      },
      methods:{

        //  获取信息
        studentMsg() {
          const result = JSON.parse(localStorage.getItem('result'));
          console.log(result);
            this.student_name = result.data[0].student_name;
            this.student_class = result.data[0].student_class;
            this.student_department = result.data[0].student_department;
            this.student_major = result.data[0].student_major;
            this.student_id = result.data[0].student_id;
            this.student_address= result.data[0].student_address,
            this.student_birthplace = result.data[0].student_birthplace,
            this.student_join_time = result.data[0].student_join_time,
            this.student_sex = result.data[0].student_sex,
            this.student_from = result.data[0].student_from,
            this.student_political_status = result.data[0].student_political_status,
            this.student_idcard = result.data[0].student_idcard
        },
      }

    }
</script>

<style scoped>

  .nonebox{
    height: 25px;
  }

  tr{
    margin: 10px;
    height: 35px;
  }

  td{
    margin: 10px;
    width:650px;
    height: 25px;
    font-size: 15px;
    font-size: large;
    border-bottom: 1px solid black;
  }

</style>



